import { deflate } from "pako";

import { REDIRECT_URL } from "@/constants";

export const returnPaymentURL = (sessionId: string, publicApiKey: string) => {
  const payload = {
    sessionId,
    publicApiKey,
  };

  const compressed = deflate(JSON.stringify(payload));
  const base64 = Buffer.from(compressed).toString("base64");
  const url_safe_base64 = encodeURIComponent(base64);

  return `${REDIRECT_URL}/pay?data=${url_safe_base64}`;
};
